@import "header";
@import "info";
@import "spec";

.doctor__before {
  margin-top: 100px;
  @include r($mobile) {
    margin-top: 30px; } }

.doctor__another__row {
  display: flex;
  margin-left: 90px;
  @include r($tablet) {
    margin-left: 0; }
  @include r($mobile) {
    flex-direction: column; }
  article {
    margin-right: 120px;
    @include rmin(1920) {
      margin-right: 180px; }
    @include r($tablet) {
      margin-right: 0;
      flex-direction: column;
      width: 50%; }
    header {
      height: 260px;
      @include rmin(1920) {
        height: 400px; }
      @include r($tablet) {
        width: 100%; } }
    section {
      @include r($tablet) {
        width: 100%; }
      .btn {
        width: 190px;
        height: 30px;
        font-size: 9px;
        left: 50%;
        transform: translateX(-50%);
        @include rmin(1920) {
          width: 280px;
          height: 45px;
          font-size: 14px; }
        i {
          font-size: 14px;
          @include rmin(1920) {
            font-size: 21px;
            margin-right: 11px; } } } } } }
