.certificate {
  padding-top: 150px;
  @include r($desktop) {
    padding-top: 100px; }
  @include r($tablet) {
    padding-top: 70px; }
  @include r($mobile) {
    padding: 50px 0;
    position: relative;
    overflow: hidden;
    .col-xs-12 {
      position: relative; } }
  &__row {
    background-color: #e7ecf2;
    height: 280px;
    @include r($desktop) {
      height: 190px; } }
  .owl-carousel {
    margin-top: 35px;
    @include r($desktop) {
      margin-top: 18px; }
    @include r($mobile) {
      margin-top: 0; } }
  .item {
    background: #ffffff;
    padding: 10px;
    height: 220px;
    @include r($desktop) {
      height: 155px;
      img {
        height: 100%;
        width: initial; } }
    @include r($mobile) {
      width: 106%;
      height: 100%;
      display: flex;
      justify-content: center;
      margin: 0 -3%;
      img {
        height: 200px;
        width: initial; } } }
  .owl-stage-outer {
    @include r($mobile) {
      overflow: visible; } }
  &__prev, &__next {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 55px;
    height: 55px;
    background: $white;
    @include center;
    z-index: 10;
    i {
      font-size: 20px;
      color: #8c9eb3; } }
  &__prev {
    left: 15px;
    i {
      transform: rotate(180deg);
      display: inline-block; } }
  &__next {
    right: 15px; } }
