.services__link {
  margin-bottom: 100px;
  @include rmin(1920) {
    margin-bottom: 140px; }
  @include r($tablet) {
    margin-bottom: 60px; }
  @include r($mobile) {
    margin-bottom: 30px; }
  &__row {
    display: flex;
    justify-content: space-between;
    @include r($mobile) {
      flex-direction: column; } }
  &__block {
    display: block;
    height: 240px;
    width: 100%;
    text-decoration: none;
    color: $white;
    position: relative;
    @include tr;
    @include cover;
    @include rmin(1920) {
      height: 360px; }
    @include r($tablet) {
      height: 160px; }
    @include r($mobile) {
      height: 50px;
      margin-bottom: 10px;
      display: flex;
      align-items: center; }
    &:before {
      content: '';
      @include coverdiv;
      background-image: linear-gradient(128deg, $blue2, $aqua 97%);
      opacity: 0;
      @include tr; }
    &:not(:last-child) {
      margin-right: 10px; }
    p {
      font-size: 30px;
      font-weight: bold;
      position: absolute;
      bottom: 25px;
      left: 30px;
      @include tr;
      @include rmin(1920) {
        font-size: 45px; }
      @include r($tablet) {
        font-size: 24px;
        left: 10px; }
      @include r($mobile) {
        transform: translate(0) !important;
        position: static;
        padding-left: 0;
        font-size: 20px;
        padding-left: 10px; } }
    &.active {
      &:before {
        opacity: 0.65; }
      p {
        bottom: 48%;
        transform: translateY(50%); } } } }

