.subscribe {
  background-image: url('../img/bg-subscribe.jpg');
  background-size: cover;
  padding-top: 100px;
  padding-bottom: 85px;
  position: relative;
  @include r($desktop) {
    padding-top: 70px;
    padding-bottom: 60px; }
  @include r($tablet) {
    padding-top: 60px;
    padding-bottom: 50px; }
  @include r($mobile) {
    padding-top: 40px;
    padding-bottom: 50px; }
  &:before {
    content: '';
    background-image: url('../img/phone.png');
    background-size: cover;
    display: block;
    width: 370px;
    height: 470px;
    position: absolute;
    right: 100px;
    top: -68px;
    z-index: 10;
    @include r($desktop) {
      width: 255px;
      height: 320px;
      top: -41px; }
    @include r($tablet) {
      content: '';
      width: 330px;
      height: 420px;
      right: 2px;
      top: 90px; }
    @include r($mobile) {
      content: none; } }
  h2 {
    font-size: 35px;
    color: #292929;
    margin-bottom: 50px;
    width: 100%;
    @include r($desktop) {
      font-size: 24px;
      margin-bottom: 35px; }
    @include r($tablet) {
      font-size: 25px;
      margin-bottom: 40px; }
    @include r($mobile) {
      font-size: 18px;
      margin-bottom: 30px;
      text-align: center; } }
  form {
    display: flex;
    flex-wrap: wrap;
    @include r($tablet) {
      flex-direction: column;
      margin-bottom: 50px; }
    input {
      width: 430px;
      margin-right: 20px;
      @include r($desktop) {
        width: 290px;
        margin-right: 13px;
        height: 45px; }
      @include r($tablet) {
        width: 345px; }
      @include r($mobile) {
        width: 100%; } }
    button {
      width: 280px;
      height: 65px;
      @include r($desktop) {
        width: 185px;
        height: 45px; }
      @include r($tablet) {
        width: 345px; }
      @include r($mobile) {
        width: 100%; } } }
  .social__info {
    position: relative;
    bottom: 20px;
    margin-left: 70px;
    @include r($desktop) {
      bottom: 15px; }
    @include r($tablet) {
      bottom: 0; }
    a {
      color: #292929; }
    @include r($mobile) {
      margin-left: 0;
      p {
        font-size: 16px;
        text-align: center; }
      .flex {
        flex-wrap: wrap;
        justify-content: center; }
      a {
        justify-content: center;
        margin-bottom: 20px;
        font-size: 16px;
        margin-right: 10px;
        i {
          font-size: 26px; }
        &:nth-of-type(1), &:nth-of-type(2) {} } } }
  .response__block {
    align-items: flex-start;
    p {
      text-align: left; } } }
