.contact {
  padding-top: 150px;
  @include r($desktop) {
    padding-top: 100px; }
  @include r($tablet) {
    padding-top: 70px; }
  @include r($mobile) {
    padding-top: 50px; }
  &__top {
    margin-bottom: 50px;
    @include r($desktop) {
      margin-bottom: 30px; }
    @include r($tablet) {
      margin-bottom: 20px; } }
  h2 {
    margin-bottom: 0 !important; }
  hr {
    border: solid 2px rgba(229, 230, 240, 0.64);
    margin-bottom: 60px;
    @include r($tablet) {
      margin-bottom: 40px; } }
  &__info {
    display: flex;
    align-items: center;
    @include r($tablet) {
      margin-bottom: 50px; }
    i {
      font-size: 55px;
      color: #cfcece;
      margin-right: 30px;
      @include r($desktop) {
        font-size: 36px;
        margin-right: 20px; }
      @include r($tablet) {
        font-size: 38px; }
      @include r($mobile) {
        font-size: 28px;
        margin-right: 15px; } }
    a {
      font-size: 25px;
      text-decoration: none;
      color: $black-two;
      @include r($desktop) {
        font-size: 17px; }
      @include r($tablet) {
        margin-right: 20px; }
      @include r($mobile) {
        font-size: 16px; } }
    a[href^="mailto:"] {
      @include r($desktop) {
        font-size: 15px; } }
    p {
      font-size: 22px;
      line-height: 1.45;
      @include r($desktop) {
        font-size: 15px; }
      @include r($tablet) {
        font-size: 18px;
        display: inline-block;
        width: 85%;
        br {
          display: none; } }
      @include r($mobile) {
        font-size: 16px;
        width: 93%; } } }
  &__link {
    font-size: 24px;
    color: #32a3c3;
    font-weight: 400;
    margin-left: 20px;
    @include r($desktop) {
      font-size: 16.5px; }
    &.active {
      color: $black;
      text-decoration: none;
      pointer-events: none; } } }


#map {
  margin-top: 55px;
  height: 475px;
  @include r($desktop) {
    height: 320px; }
  @include r($tablet) {
    margin-top: 0; } }
