.btn {
	border-radius: 4px;
	color: $white;
	text-transform: uppercase;
	font-size: 19.5px;
	text-decoration: none;
	@extend .ubuntu;
	cursor: pointer;
	@include center;
	@include tr;
	@include r($desktop) {
		font-size: 12.5px;
		height: 55px; }
	@include r($tablet) {
		font-size: 17.5px; }
	@include r($mobile) {
		font-size: 19.5px; }
	span, i {
		z-index: 100;
		position: relative; }
	i {
		font-size: 28px;
		margin-right: 30px;
		@include r(1679) {
			margin-right: 5px; }
		@include r(1279) {
			margin-right: 5px; } }
	&-primary {
		background-color: $blue;
		position: relative;
		&:before {
			content: '';
			background-image: linear-gradient(94deg, $blue2, $aqua 97%);
			width: 100%;
			height: 100%;
			display: block;
			position: absolute;
			top: 0;
			left: 0;
			opacity: 0;
			border-radius: 4px;
			@include tr; }
		&:hover:before {
			opacity: 1; } }
	&-secondary {
		background-color: transparent;
		border: 3px solid $white;
		position: relative;
		&:before {
			content: '';
			background-image: linear-gradient(94deg, $blue2, $aqua 97%);
			width: 100%;
			height: 100%;
			display: block;
			position: absolute;
			top: 0;
			left: 0;
			opacity: 0;
			@include tr; }
		&:hover:before {
			opacity: 1; } }
	&-third {
		background-color: transparent;
		border: 3px solid $white;
		position: relative;
		&:before {
			content: '';
			background-image: linear-gradient(94deg, $blue2, $aqua 97%);
			width: 100%;
			height: 100%;
			display: block;
			position: absolute;
			top: 0;
			left: 0;
			opacity: 0;
			@include tr; }
		&:hover:before {
			opacity: 1; } } }

