.doctors {
  padding: 100px 0;
  overflow: hidden;
  @include r($desktop) {
    padding: 65px 0; }
  @include r($tablet) {
    padding: 70px 0; }
  @include r($mobile) {
    padding: 50px 0; }
  article {
    width: 33.33%;
    @include tr;
    cursor: pointer;
    @include r($tablet) {
      width: 100%;
      display: flex;
      flex-wrap: wrap; }
    @include r($mobile) {
      width: 110%;
      margin: 0 -15px; }
    header {
      overflow: hidden;
      position: relative;
      height: 400px;
      @include r($desktop) {
        height: 268px; }
      @include r($tablet) {
        height: 350px;
        width: 40%; }
      @include r($mobile) {
        width: 100%;
        height: 250px; }
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        @include r($tablet) {
          position: absolute;
          left: 50%;
          transform: translateX(-50%); } }
      h1 {
        position: absolute;
        bottom: 35px;
        left: 60px;
        color: $white;
        font-size: 28px;
        @include r($desktop) {
          font-size: 19px;
          bottom: 20px;
          left: 45px; }
        @include r($tablet) {
          font-size: 24px;
          bottom: 30px;
          left: 15px; }
        @include r($mobile) {
          font-size: 20px;
          bottom: 20px;
          left: 30px; } } }
    section {
      padding: 40px 65px;
      @include r($desktop) {
        padding: 24px 44px; }
      @include r($tablet) {
        width: 60%;
        padding: 38px 28px; }
      @include r($mobile) {
        display: none; }
      p {
        font-size: 20px;
        color: #3f3f3f;
        line-height: 1.35;
        @extend .roboto;
        margin-bottom: 35px;
        font-weight: 300;
        @include r($desktop) {
          font-size: 13.5px;
          margin-bottom: 22px; }
        @include r($tablet) {
          margin-bottom: 50px; }
        strong {
          @extend .ubuntu;
          font-weight: 700; } }
      div {
        margin-bottom: 40px;
        @include r($desktop) {
          margin-bottom: 30px; }
        a {
          color: #3f3f3f;
          display: flex;
          align-items: center;
          text-decoration: none;
          font-size: 20px;
          text-transform: uppercase;
          @include r($desktop) {
            font-size: 13.5px; }
          i {
            font-size: 34px;
            margin-right: 15px;
            @include r($desktop) {
              font-size: 23px;
              margin-right: 8px; } } } }
      .btn {
        width: 100%;
        height: 60px; } }
    &:hover {
      box-shadow: 0px 3px 87px 0 #cfd7e4; } } }

