.doctor__header {
  background-image: linear-gradient(55deg, $blue2, #126ad6 97%);
  .row {
    justify-content: space-between; }
  &__image {
    @include rmin(1920) {
      width: 500px; }
    @include r($mobile) {
      position: relative;
      left: 50%;
      transform: translateX(-50%);
      width: 65%; }
    img {
      width: 100%;
      height: 100%;
      object-fit: cover; } }
  &__block {
    margin: 65px 0;
    padding-left: 80px;
    @include rmin(1920) {
      margin: 100px 0; }
    @include r($mobile) {
      padding-left: 15px;
      margin: 25px 0; }
    h1 {
      font-size: 24px;
      color: #fff;
      margin-bottom: 15px;
      @include rmin(1920) {
        font-size: 36px;
        margin-bottom: 25px;
        @include r($mobile) {
          font-size: 20px; } } }
    p {
      font-size: 18px;
      font-weight: 300;
      color: #fff;
      @include rmin(1920) {
        font-size: 27px; }
      @include r($mobile) {
        font-size: 14px; } } }
  &__info {
    margin-top: 30px;
    p {
      font-size: 18px;
      font-weight: normal;
      color: #fff;
      @include rmin(1920) {
        font-size: 25px; }
      &:first-child {
        margin-bottom: 15px;
        @include rmin(1920) {
          margin-bottom: 25px; } }
      b {
        font-weight: bold;
        @include rmin(1920) {
          font-size: 27px; } } } } }





