.services__content {
	overflow: hidden;
	h2 {
		@include rmin(1920) {
			font-size: 36px; } }
	.heading {
		width: 100%; }
	.tabs {
		margin-top: 40px;
		@include rmin(1920) {
			margin-top: 55px; }
		&__caption {
			display: flex;
			justify-content: space-around;
			margin-bottom: 45px;
			background: 0 !important;
			@include r($mobile) {
				flex-direction: column;
				margin-bottom: 20px;
				padding: 10px;
				flex-direction: row;
				justify-content: flex-start;
				overflow-y: scroll;
				box-sizing: border-box;
				@include cover; }
			li {
				font-size: 18px;
				font-weight: bold;
				color: #a7a8ae;
				cursor: pointer;
				@include tr;
				@include rmin(1920) {
					font-size: 27px; }
				@include r($mobile) {
					text-align: center;
					margin-bottom: 5px;
					font-size: 16px;
					margin-right: 10px;
					background: $blue;
					padding: 5px;
					color: $white;
					display: flex;
					justify-content: center;
					align-items: center; }
				&.active {
					color: $black;
					@include r($mobile) {
						color: $white;
						background: darken($blue, 20%); } } } }
		&__content {
			@include r($tablet) {
				padding: 0 20px; }
			@include r($mobile) {
				padding: 0; } }
		&__image {
			width: 470px;
			height: 290px;
			margin-right: 40px;
			@include rmin(1920) {
				width: 700px;
				height: 420px; }
			@include r($tablet) {
				width: 100%;
				margin-right: 0; }
			@include r($mobile) {
				padding: 0 8px; }
			img {
				width: 100%;
				height: 100%;
				object-fit: cover; } }
		&__about {
			flex: 1;
			@include r($tablet) {
				width: 100%;
				padding: 20px 20px; }
			&__title {
				margin-top: 20px;
				@include rmin(1920) {
					margin-top: 25px; }
				.tabs__caption {
					margin-bottom: 20px;
					justify-content: flex-start;
					@include rmin(1920) {
						margin-bottom: 28px; }
					@include r($mobile) {
						flex-direction: row;
						justify-content: center; }
					li {
						font-size: 16px;
						font-weight: bold;
						color: #a7a8ae;
						@include tr;
						@include rmin(1920) {
							font-size: 24px; }
						@include r($mobile) {
							color: $white; }
						&.active {
							color: $black;
							@include r($mobile) {
								color: $white;
								background: darken($blue, 20%); } } }
					span {
						margin: 0 30px;
						display: inline-block;
						font-size: 16px;
						font-weight: 500;
						color: #a7a8ae;
						@include rmin(1920) {
							font-size: 24px; }
						@include r($mobile) {
							display: none; } } }
				p {
					font-size: 14px;
					font-weight: 300;
					color: #3f3f3f;
					margin-bottom: 20px;
					@include rmin(1920) {
						font-size: 21px; } }
				ul {
					margin-bottom: 20px;
					li {
						font-size: 14px;
						font-weight: 300;
						color: #3f3f3f;
						@include rmin(1920) {
							font-size: 21px; } } } } }
		&__price {
			margin: 100px 0;
			width: 100%;
			@include rmin(1920) {
				margin: 150px 0; }
			@include r($tablet) {
				margin: 20px 0; }
			@include r($mobile) {
				height: 100%; }
			h3 {
				font-size: 23.5px;
				font-weight: 500;
				color: $black-four;
				text-align: center;
				margin-bottom: 30px;
				@include rmin(1920) {
					font-size: 35.5px;
					margin-bottom: 55px; } }
			&__item {
				display: flex;
				justify-content: space-between;
				align-items: center;
				height: 35px;
				width: 100vw;
				position: relative;
				left: 50%;
				right: 50%;
				margin-left: -50vw;
				margin-right: -50vw;
				padding: 0 130px;
				@include rmin(1920) {
					height: 50px;
					padding: 0 190px; }
				@include r($tablet) {
					padding: 0 20px;
					height: 60px; }
				@include r($mobile) {
					height: 100%;
					padding: 10px 20px; }
				&:nth-of-type(even) {
					height: 50px;
					background-color: #f8f8f8;
					@include rmin(1920) {
						height: 75px; }
					@include r($tablet) {
						height: 50px; }
					@include r($mobile) {
						height: 100%; } }

				p {
					font-size: 13.5px;
					font-weight: 300;
					line-height: 2.07;
					text-align: left;
					color: #3b3b3b;
					@include rmin(1920) {
						font-size: 20px; }
					@include r($mobile) {
						padding-right: 10px; } }
				span {
					font-size: 16.5px;
					color: $black-four;
					font-weight: 500;
					@include rmin(1920) {
						font-size: 24.5px; }
					@include r($tablet) {
						font-size: 14px;
						white-space: nowrap; } } }
			hr {
				border: solid 1px #e1e1e1;
				margin-top: 30px;
				position: relative;
				left: 50%;
				right: 50%;
				margin-left: -50vw;
				margin-right: -50vw;
				margin-bottom: 20px; }
			&__credit {
				display: flex;
				justify-content: flex-end;
				align-items: center;
				@include rmin(1920) {
					padding-right: 130px; }
				p {
					font-size: 14px;
					line-height: 1.36;
					color: #9fa0a6;
					font-weight: normal;
					margin-right: 15px;
					@include rmin(1920) {
						font-size: 21px; }
					@include r($mobile) {
						font-size: 12px; } }
				a {
					width: 165px;
					height: 37px;
					@include center;
					font-size: 14px;
					color: $blue;
					border: 1px solid $blue;
					border-radius: 4px;
					text-decoration: none;
					font-weight: normal;
					@include rmin(1920) {
						font-size: 21px;
						width: 250px;
						height: 60px; } } } }
		&__before {
			@include r($mobile) {
				width: 100%; }
			.heading h2 {
				margin-bottom: 30px; }
			&__primary {
				margin-top: 0px;
				@include rmin(1920) {
					margin-top: 10px; }
				.tabs__caption {
					display: block;
					margin-bottom: 15px;
					margin-left: 15px;
					overflow: scroll;
					-webkit-overflow-scrolling: touch;
					@include r($mobile) {
						display: flex;
						flex-direction: row;
						margin-left: 0; }
					li {
						font-size: 17px;
						font-weight: bold;
						color: #3f3f3f;
						display: inline-block;
						margin-right: 20px;
						font-weight: normal;
						@include rmin(1920) {
							font-size: 25.5px; }
						@include tr;
						&.active {
							font-weight: bold; }
						@include r($mobile) {
							background: $blue;
							color: $white;
							white-space: nowrap;
							padding: 5px;
							border-radius: 4px; } } } }
			&__secondary {
				margin-top: 0;
				display: flex;
				flex-direction: column-reverse;
				@include rmin(1920) {
					margin-top: 25px; }
				.tabs__caption {
					margin-top: 20px;
					margin-left: 23px;
					overflow: scroll;
					-webkit-overflow-scrolling: touch;
					@include r($mobile) {
						justify-content: flex-start; }
					li {
						position: relative;
						@include tr;
						@include r($mobile) {
							margin-right: 10px; }
						&:before {
							content: '|';
							position: absolute;
							right: -14px;
							font-weight: normal;
							@include r($mobile) {
								content: none; } } } } }
			&__images {
				width: 820px;
				height: 385px;
				@include rmin(1920) {
					width: 1230px;
					height: 555px; }
				@include r($tablet) {
					width: 100%;
					height: 325px; }
				@include r($mobile) {
					height: 100%; }
				&__block {
					display: flex;
					justify-content: space-between;
					@include r($mobile) {
						flex-direction: column; }
					img:nth-of-type(1) {
						margin-right: 20px; }
					img {
						width: 100%;
						height: 100%;
						object-fit: cover;
						@include r($tablet) {
							width: 50%; }
						@include r($mobile) {
							width: 100%;
							margin-bottom: 10px; } } } }
			&__about {
				flex: 1;
				margin-left: 30px;
				position: relative;
				@include r($mobile) {
					margin-left: 0;
					padding: 0 20px; }
				h2 {
					font-size: 17px;
					font-weight: bold;
					line-height: 4;
					text-align: left;
					color: #3f3f3f;
					margin-bottom: 20px;
					@include rmin(1920) {
						font-size: 25.5px; }
					@include r($mobile) {
						margin-bottom: 5px;
						line-height: 1; } }
				p {
					font-size: 14px;
					font-weight: 300;
					color: #3f3f3f;
					@include rmin(1920) {
						font-size: 21px; } }
				.btn {
					width: 200px;
					height: 45px;
					font-size: 14.5px;
					bottom: 55px;
					position: absolute;
					right: 0;
					@include r($mobile) {
						position: static;
						margin-top: 10px; }
					@include rmin(1920) {
						width: 300px;
						height: 65px;
						font-size: 21.5px;
						bottom: 65px; } } }
			&__review {
				margin-top: 30px;
				@include rmin(1920) {
					margin-bottom: 50px; }
				@include r($mobile) {
					padding: 20px; }
				h2 {
					font-size: 17px;
					color: #3f3f3f;
					@include rmin(1920) {
						font-size: 25.5px; } }
				p {
					font-size: 14px;
					font-weight: 300;
					color: #3f3f3f;
					padding: 70px 120px;
					@include rmin(1920) {
						font-size: 21px; }
					@include r($tablet) {
						padding: 50px; } }
				.tabs__review__block {
					position: relative;
					margin: 20px 100px;
					@include rmin(1920) {
						margin: 20px 200px; }
					@include r($mobile) {
						margin: 0px; }
					&:before, &:after {
						content: $icon-quote;
						position: absolute;
						font-size: 150px;
						color: #e7ecf2;
						z-index: -1;
						@extend %icon; }
					&:before {
						top: 0;
						left: 0;
						transform: scale(-1, 1); }
					&:after {
						right: 0;
						bottom: 0;
						transform: rotate(180deg) scale(-1, 1); } } } }
		&__doctors {
			width: calc(50% - 75px);
			text-decoration: none;
			display: flex;
			flex-wrap: wrap;
			margin-bottom: 110px;
			&:not(:last-child) {
				margin-right: 150px;
				@include r($mobile) {
					margin-right: 0; } }
			@include r($tablet) {
				display: flex;
				align-items: center;
				flex-direction: column;
				margin-bottom: 50px; }
			@include r($mobile) {
				width: 100%;
				position: relative;
				left: 50%;
				transform: translateX(-50%);
				padding: 0 50px; }
			&__image {
				width: 150px;
				height: 150px;
				overflow: hidden;
				border-radius: 50%;
				margin-right: 20px;
				@include rmin(1920) {
					width: 200px;
					height: 200px; }
				@include r($tablet) {
					margin-bottom: 20px; }
				@include r($mobile) {
					margin-right: 0; }
				img {
					width: 100%;
					height: 100%;
					object-fit: cover; } }
			&__content {
				flex: 1;
				p {
					font-size: 13px;
					font-weight: 300;
					color: #3f3f3f;
					margin-bottom: 10px;
					@include rmin(1920) {
						font-size: 19.5px;
						margin-bottom: 15px; }
					@include r($tablet) {
						text-align: center; } }
				span {
					font-size: 11px;
					width: 35%;
					display: block;
					margin-bottom: 5px;
					@include rmin(1920) {
						font-size: 16.5px; }
					@include r($tablet) {
						text-align: center;
						width: 100%;
						margin-bottom: 15px; } }
				.btn {
					width: 185px;
					height: 30px;
					float: right;
					text-align: center;
					@include rmin(1920) {
						width: 280px;
						height: 45px; }
					@include r($tablet) {
						float: none;
						position: relative;
						left: 50%;
						transform: translateX(-50%); }
					i {
						font-size: 15px;
						@include rmin(1920) {
							font-size: 20px;
							margin-right: 12px; } }
					span {
						width: initial;
						font-size: 9px;
						margin-bottom: 0;
						@include rmin(1920) {
							font-size: 13.5px; } } } } }
		&__decision {
			&__image {
				width: 415px;
				height: 240px;
				background: $black;
				margin-right: 30px;
				margin-bottom: 30px;
				@include rmin(1920) {
					width: 630px;
					height: 360px;
					margin-right: 45px;
					margin-bottom: 45px; }
				@include r(1279) {
					height: 340px; }
				@include r($tablet) {
					width: 100%;
					height: 300px;
					margin-right: 0; } }
			&__text {
				flex: 1;
				h3 {
					font-size: 23.5px;
					font-weight: 500;
					color: $black-four;
					margin-bottom: 20px;
					margin-top: 15px;
					@include rmin(1920) {
						font-size: 36px;
						margin-bottom: 30px; } }
				&__p {
					columns: 2;
					@include r($tablet) {
						columns: 1; }
					p {
						font-size: 13px;
						font-weight: 300;
						color: #3f3f3f;
						margin-bottom: 20px;
						@include rmin(1920) {
							font-size: 19.5px; } } } }
			&__articles {
				margin-bottom: 45px;
				width: 755px;
				margin-right: 30px;
				@include rmin(1920) {
					width: 1200px;
					margin-right: 90px; }
				@include r(1279) {
					width: 450px;
					top: 20px;
					position: relative; }
				@include r($tablet) {
					width: 100%;
					margin-top: 20px;
					+ .tabs__decision__recomended {
						display: none; } }
				@include r($mobile) {
					margin-right: 0px; }
				&__item {
					font-size: 16.5px;
					color: $black-four;
					text-decoration: none;
					display: flex;
					align-items: center;
					font-weight: normal;
					position: relative;
					padding: 10px 0;
					@include tr;
					@include rmin(1920) {
						font-size: 24.5px; }
					&:nth-of-type(even) {
						background-color: #f2f3f9; }
					&:hover {
						color: $blue; } }
				.btn {
					width: 200px;
					height: 45px;
					margin-top: 40px;
					@include rmin(1920) {
						width: 300px;
						height: 65px; }
					span {
						font-size: 14.5px;
						@include rmin(1920) {
							font-size: 21.5px; } } } }
			&__recomended {
				width: 430px;
				position: relative;
				@include center;
				flex-direction: column;
				text-decoration: none;
				@include rmin(1920) {
					margin-bottom: 70px; }
				h3 {
					font-size: 24px;
					color: #3b3a3a;
					text-align: center;
					margin-bottom: 20px;
					@include rmin(1920) {
						font-size: 36px; } }
				&__image {
					width: 370px;
					height: 270px;
					overflow: hidden;
					@include rmin(1920) {
						width: 560px;
						height: 400px; }
					@include r(1279) {
						width: 290px; }
					@include r($tablet) {
						width: 225px; }
					@include r($mobile) {
						width: 100%;
						margin-bottom: 10px; }
					img {
						width: 100%;
						height: 100%;
						object-fit: cover; } }
				span {
					width: 430px;
					height: 70px;
					font-size: 22px;
					background-color: rgba(#59b2d2, 0.85);
					opacity: 0.85;
					color: $white;
					@include center;
					position: absolute;
					bottom: 40px;
					left: 50%;
					transform: translateX(-50%);
					@include rmin(1920) {
						width: 650px;
						height: 100px;
						font-size: 33px; } }
				&__row {
					width: 100%;
					display: flex;
					flex-wrap: wrap;
					position: relative;
					margin-bottom: 50px;
					.tabs__decision__recomended {
						width: 33.33%;
						@include r($mobile) {
							width: 100%; } }
					span {
						width: 97vw;
						box-sizing: border-box;
						height: 70px;
						font-size: 22px;
						background-color: rgba(#59b2d2, 0.85);
						opacity: 0.85;
						color: $white;
						@include center;
						position: absolute;
						bottom: 40px;
						left: 50%;
						transform: translateX(-50%);
						@include rmin(1920) {
							bottom: 100px; }
						@include r($mobile) {
							text-align: center;
							width: 100vw; } } } } } } }






