form {
  input {
    background: none;
    border: 1px solid #b2b2b2;
    border-radius: 6px;
    display: block;
    width: 100%;
    box-sizing: border-box;
    height: 65px;
    display: flex;
    align-items: center;
    padding-left: 20px;
    font-size: 18.5px;
    @extend .roboto;
    @include tr;
    color: $white;
    &:not(:last-child) {
      margin-bottom: 20px;
      @include r($desktop) {
        margin-bottom: 13px; } }
    @include r($desktop) {
      font-size: 12.5px;
      height: 54px; }
    &:focus {
      box-shadow: 0px 0px 10px 1px $white;
      border-color: $white; } }
  button {
    width: 100%; } }

.response {
  display: none;
  height: 100%;
  &__block {
    @include center;
    flex-direction: column;
    height: 100%;
    i {
      font-size: 65px;
      color: #b8b9bd;
      margin-bottom: 20px; }
    h2 {
      font-size: 30px;
      margin-bottom: 10px; }
    p {
      font-size: 16px;
      text-align: center;
      font-weight: 500; } }
  .social__info {
    margin-left: 0; } }
