.intro {
	background-image: url('../img/bg-intro.jpg');
	background-size: cover;
	height: 560px;
	position: relative;
	overflow: hidden;
	@include r($desktop) {
		height: 380px; }
	@include r($tablet) {
		height: 415px; }
	@include r($mobile) {
		height: 270px; }
	&__text {
		color: $white;
		position: absolute;
		bottom: 70px;
		@include r($tablet) {
			margin-left: 0;
			position: static;
			margin-top: 100px; }
		@include r($mobile) {
			margin-top: 90px; }
		p {
			font-size: 26px;
			@include r($desktop) {
				font-size: 18px; }
			@include r($mobile) {
				font-size: 16px; } }
		h2 {
			font-size: 55.5px;
			line-height: 1.16;
			position: relative;
			@include r($desktop) {
				font-size: 37.5px; }
			@include r($tablet) {
				font-size: 44.5px;
				margin-bottom: 50px; }
			@include r($mobile) {
				font-size: 28.5px;
				margin-bottom: 20px; }
			&:before {
				content: '';
				@include s('arrow');
				display: block;
				position: absolute;
				right: -210px;
				top: -150px;
				@include r(1439) {
					right: -150px; }
				@include r($tablet) {
					content: none; } } }
		.btn {
			width: 350px;
			height: 70px;
			float: right;
			@include r($mobile) {
				width: 100%;
				height: 50px;
				float: none;
				font-size: 16.5px; } } }
	&__form {
		background: $white;
		padding: 45px 60px;
		margin-top: 50px;
		@include r($desktop) {
			padding: 30px 40px;
			margin-top: 14px; }
		form {
			input {
				color: #000000;
				&:hover, &:focus {
					box-shadow: 0px 0px 10px 1px $blue; } }
			h3 {
				font-size: 32px;
				margin-bottom: 35px;
				color: #3f3f3f;
				@include r($desktop) {
					font-size: 21.5px;
					margin-bottom: 25px;
					br {
						display: none; } } }
			p {
				margin-top: 30px;
				font-weight: 300;
				@extend .roboto;
				font-size: 18.5px;
				color: $black-three;
				@include r($desktop) {
					font-size: 12.5px;
					margin-top: 10px; } }
			.btn {} } } }
