.news {
	a {
		text-decoration: none;
		color: $black; }
	.row {
		justify-content: space-between; }
	&__row {
		&__block {
			display: flex;
			flex-direction: column;
			flex: 1;
			margin-left: 35px;
			@include rmin(1920) {
				margin-left: 50px; }
			@include r($mobile) {
				margin-left: 0; } } }
	&__block {
		width: 315px;
		margin-bottom: 50px;
		@include rmin(1920) {
			width: 470px;
			margin-bottom: 75px; }
		@include r(1080) {
			width: 290px; }
		@include r($tablet) {
			width: 230px; }
		@include r($mobile) {
			width: 100%; }
		img {
			width: 100%;
			object-fit: cover;
			height: 200px;
			margin-bottom: 20px;
			@include rmin(1920) {
				height: 300px; } }
		h1 {
			font-size: 17px;
			margin-bottom: 20px;
			font-weight: 400;
			@include rmin(1920) {
				font-size: 26px; } }
		p {
			font-size: 14px;
			font-weight: 300;
			max-height: 144px;
			overflow: hidden;
			@include rmin(1920) {
				font-size: 21px;
				max-height: 197px; } }
		&__big {
			width: 500px;
			height: 460px;
			position: relative;
			display: flex;
			align-items: flex-end;
			@include rmin(1920) {
				width: 750px;
				height: 700px; }
			@include r(1200) {
				width: 400px; }
			@include r($tablet) {
				width: 100%; }
			@include r($mobile) {
				height: 100%;
				padding-top: 30px; }
			img {
				position: absolute;
				width: 100%;
				height: 100% !important;
				top: 0; }
			h1, p {
				color: $white;
				position: relative;
				padding-left: 30px;
				padding-right: 70px; }
			p {
				margin-bottom: 30px; } }
		&__small {
			width: 100% !important;
			margin-bottom: 37px;
			article {
				display: flex;
				flex-wrap: wrap;
				@include r($mobile) {
					flex-direction: column; }
				header {
					width: 260px;
					margin-right: 35px;
					@include rmin(1920) {
						width: 380px; }
					@include r(1080) {
						width: 200px; }
					@include r($mobile) {
						width: 100%;
						margin-right: 0; } }
				section {
					flex: 1; } } } }
	&__services {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		width: 100%;
		height: 100%;
		position: relative;
		margin-bottom: 100px;
		@include rmin(1920) {
			margin-bottom: 140px; }
		@include r($tablet) {
			flex-wrap: nowrap;
			margin-bottom: 50px; }
		@include r($mobile) {
			flex-direction: column; }
		span {
			width: 110%;
			box-sizing: border-box;
			height: 70px;
			font-size: 22px;
			background-color: rgba(#59b2d2, 0.85);
			opacity: 0.85;
			color: $white;
			@include center;
			position: absolute;
			bottom: 40px;
			left: 50%;
			transform: translateX(-50%);
			@include rmin(1920) {
				bottom: 100px;
				font-size: 33px;
				height: 103px; }
			@include r($mobile) {
				text-align: center;
				width: 100vw; } } }
	&__service {
		@include r($tablet) {
			width: 100%;
			margin-right: 10px;
			article {
				width: 100%; } }
		img {
			width: 100%;
			@include rmin(1920) {
				width: 550px;
				height: 400px; } }
		h1 {
			text-align: center;
			margin-bottom: 20px;
			font-size: 24px;
			font-weight: bold;
			color: #3b3a3a;
			@include rmin(1920) {
				font-size: 36px; } } } }





